<template>
  <section class="confirmation-area section-bg-2 pat-100 pab-100">
    <div class="container">
      <div class="confirmation-contents center-text">
        <div class="confirmation-contents-icon">
          <i class="las la-check"></i>
        </div>
        <h4 class="confirmation-contents-title">   . </h4>
        <p class="confirmation-contents-para">  {{ rvno }} .    .<br/>
                . <br/>   ! </p>
        <div class="btn-wrapper flex-btn mt-4 mt-lg-5">
          <router-link to="/" class="cmn-btn btn-outline-1 color-one"> </router-link>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import {useReservationStore} from "@/store/ReservationStore";

export default {
  name : "ReservationSuccessComponent",
  data() {
    return { rvno : "" }
  },
  methods : {
    getRvno(){
      const reservationStore = useReservationStore();
      this.rvno = reservationStore.rvno;
    },
    resetRvno() {
      const reservationStore = useReservationStore();
      reservationStore.setRvno(null);
    }
  },
  mounted() {
    this.getRvno();
  },
  unmounted() {
    this.resetRvno();
  }
}
</script>

